@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300..800;1,300..800&family=Parkinsans:wght@300..800&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');

*{
  font-family: "Open sans", sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #2c3a47;
}
.currency-converter{
  width: 400px;
  background-color: #fff;
  padding: 10px 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
}
h1{
  font-weight: 600;
  text-transform: uppercase;
  font-size: 20px;
  padding: 10px;
  margin-bottom: 15px;
  color: #3498db;
  text-align: center;
  border-top: 1px dashed #ccc;
  border-bottom: 1px dashed #ccc;

}
.box{
  width: 100%;
  height: 200px;
  background-image: url("./assets/logo.webp");
  background-size: 50%;
  background-position: center;
  background-repeat: no-repeat;
}
.input-container{
  margin-bottom: 15px;
}
label{
  display: block;
  margin-bottom: 5px;
  color: #888;
  font-weight: 500;
}
input,
select{
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 18px;
  outline: none;
}
input:focus,
select:focus{
  border-color:  #3498db;
}
.result{
  margin: 15px 10px;
  padding: 10px;
  border: 2px dotted #3498db;
  border-radius: 4px;
}
.result p{
  margin: 8px 0;
  color: #3498db;
  font-weight: bold;
  text-align: center;
}
.footer{
  
  text-align: center;
  font-size: 12px;
  
}
.footer a{
  text-decoration: none;
  color: #3498db;
}